import React from "react";
import { Link } from "react-router-dom";
import styles from "./Header.module.css";
import logo from "../Assets/FrontEnd Developer.svg";
import logoMobile from "../Assets/FrontEnd Developer Mobile.svg";
import UseMedia from "../Hooks/UseMedia";

const Header = () => {
  const mobile = UseMedia("(max-width: 40rem)");
  const [showUl, setShowUl] = React.useState(false);
  let url = window.location.href;

  if (url === 'http://localhost:3000/' || url === 'http://localhost:3000/projetos' ||url === 'http://localhost:3000/contato') {
    window.scrollTo( 0, 0 );
  }
  console.log(url);
  

  function closeUl() {
    setTimeout(() => {
      if (showUl) {
        setShowUl(false);
      }
    });
  }

  return (
    <header className={styles.header} onBlur={closeUl}>
      <nav>
        <div className={styles.left}>
          <Link to="/">
            <img
              src={mobile ? logoMobile : logo}
              alt="Túlio Chaves"
            />
          </Link>
        </div>
        <div className={styles.right}>
          <button
            onClick={() => {
              setShowUl(!showUl);
            }}
            className={mobile ? styles.mobile : ""}
          ></button>

          <ul className={mobile ? styles.mobile : ""}>
            <li className={url === 'http://localhost:3000/' ? styles.ativo : ""}>
              <Link to="/">Quem sou</Link>
            </li>

            <li className={url === 'http://localhost:3000/projetos' ? styles.ativo : ""}>
              <Link to="/projetos">Projetos</Link>
            </li>
            <li className={styles.separador}>
              <Link className={styles.botao} to="/contato">
                Contato
              </Link>
            </li>
          </ul>

          <ul className={mobile && showUl ? styles.isMobile : styles.isNotMobile}>
            <li>
              <Link to="/">Quem sou</Link>
            </li>
            <li>
              <Link to="/projetos">Projetos</Link>
            </li>
            <li>
              <Link className={styles.botao} to="/contato">
                Contato
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
};

export default Header;
